<script>
  import { getContext } from "svelte";
  import { tweened } from "svelte/motion";

  export let d;
  export let r = 4;

  const { xScale, yScale, custom } = getContext("LayerCake");
  const pos = tweened(null, { duration: 500 });
  const { name, imageUrl1, bandRank, bandFollowers, rank, followers } = d;

  $: ratio = $custom.fixedAspectRatio;
  $: visible = $custom.visible;
  $: pos.set([bandRank, bandFollowers]);
  $: x = $xScale($pos[0]);
  $: y = $yScale($pos[1]);
  console.log(name, bandFollowers);
</script>

<div
